/* ? ======= tabs start ======= */
.tabs {
   width: 100%;
   height: fit-content;
   background-color: var(--color-azure);

   &__list-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-top: clamp(1.875rem, -4.117rem + 9.78vw, 4.688rem);
      padding-bottom: clamp(1.25rem, -2.079rem + 5.43vw, 2.813rem);

      font-family: "Ubuntu", sans-serif;
      font-size: clamp(1rem, 0.467rem + 0.87vw, 1.25rem);
      font-weight: 400;
      font-style: normal;
      color: var(--color-white);
   }

   &__item {
      position: relative;
      cursor: pointer;

      &:before {
         position: absolute;
         content: "";
         width: 0%;
         height: 2px;
         bottom: -5px;
         left: 0;
         transition: 0.5s;
      }

      &:hover:before {
         transition: 0.5s;
         width: 100%;
         background-color: var(--color-keppel);
      }

      &:hover {
         color: var(--color-keppel);
         transition: 0.5s;
      }

      &--color {
         color: var(--color-keppel);
         position: relative;

         &:before {
            position: absolute;
            content: "";
            width: 100%;
            height: 2px;
            background-color: var(--color-keppel);
            bottom: -5px;
            left: 0;
         }
      }
   }

   &__body {
      display: none;

      &-active {
         display: flex;
         justify-content: space-between;
         align-items: flex-start;
      }
   }

   &__picture {
      width: 50%;
      height: clamp(31.25rem, 17.935rem + 21.74vw, 37.5rem);

      img {
         height: 100%;
         object-fit: cover;
      }
   }

   /* ? ======= tabs settings image start ======= */

   &__slide1 {
      filter: brightness(0.7) grayscale(1);
   }

   &__slide2 {
      filter: brightness(0.4);
   }

   &__slide3 {
      filter: brightness(0.7);
   }

   &__slide4 {
      filter: sepia(50%) brightness(0.5);
   }

   /* ? ======= filter settings image end ======= */

   &__content {
      width: 50%;
      height: clamp(31.25rem, 17.935rem + 21.74vw, 37.5rem);
      padding-top: clamp(1.25rem, -6.739rem + 13.04vw, 5rem);
      padding-bottom: 0;
      padding-left: clamp(1.25rem, -6.739rem + 13.04vw, 5rem);
      padding-right: 15px;
      background-color: var(--color-dianne);

      display: flex;
      flex-direction: column;
      justify-content: space-around;
      // align-items: flex-start;

      color: var(--color-white);
   }

   &__content--left {
      align-items: flex-end;
      padding-right: clamp(1.25rem, -9.402rem + 17.39vw, 6.25rem);
      padding-left: 15px;
      padding-bottom: 0;
   }

   &__text--left {
      text-align: right;
   }

   &__title {
      margin: 0;
      font-size: clamp(1.375rem, 0.043rem + 2.17vw, 2rem);
      font-weight: 700;
      line-height: clamp(1.25rem, 0.607rem + 3.21vw, 3.5rem);
      letter-spacing: 5.12px;
      text-transform: uppercase;
   }

   &__text {
      margin: 0;
      margin-top: 24px;
      font-family: "Roboto", sans-sarif;
      font-size: clamp(1.125rem, 0.06rem + 1.74vw, 1.625rem);
      font-weight: 400;
      line-height: 150%;

      // justify-self: end;
      vertical-align: start;
   }

   &__btn {
      margin-top: 78px;
      transition: 0.5s;
   }
}

/* ? ======= tabs media start ======= */

@media screen and (max-width: 980px) {
   .tabs {
      height: fit-content;
      display: flex;

      &__container {
         width: fit-content;
      }

      &__list-header {
         height: 100%;
         flex-direction: column;
         align-items: flex-start;
         justify-content: space-around;
      }

      &__title {
         align-self: self-start;
         line-height: 150%;
      }

      &__picture {
         display: none;
      }

      &__content {
         margin: 0 auto;
         width: 100%;
         height: fit-content;
         align-items: flex-start;
         padding-bottom: 40px;
         padding-left: clamp(0.938rem, -12.26rem + 40.22vw, 12.375rem);
      }

      &__text {
         width: 70%;
         text-align: left;
      }
   }
}

@media screen and (max-width: 665px) {
   .tabs {
      &__title {
         margin-left: 0;
      }

      &__text {
         width: 100%;
      }
   }
}

@media screen and (max-width: 525px) {
   .tabs {
      &__container {
         display: none;
      }

      &__content {
         padding-left: 15px;
      }

      &__btn{
         align-self: center;
      }
   }
}
